//
// Custom Tables
// --------------------------------------------------


// Base styles
// --------------------------------------------------

// Table Custom
// -------------------------

.table-custom {
  text-align: left;

  tr {
    &:nth-child(odd) {
      background-color: $table-custom-odd-background;
    }

    td:first-child {
      font-weight: 700;
    }
  }

  // large table from 768
  &.large-only {
    tr {
      &:first-child {
        border: 1px solid $gray-500;
        background: -moz-linear-gradient(top, $white 0%, $gray-200 100%);
        background: -webkit-linear-gradient(top, $white 0%, $gray-200 100%);
        background: -o-linear-gradient(top, $white 0%, $gray-200 100%);
        background: -ms-linear-gradient(top, $white 0%, $gray-200 100%);
        background: linear-gradient(to bottom, $white 0%, $gray-200 100%);
      }
    }

    th, td {
      padding: $table-custom-large-column-padding;
    }

    @include media-breakpoint-up(lg) {
      th, td {
        &:nth-child(n+2) {
          padding-left: 56px;
        }
      }
    }
  }

  // small table to 767
  &.small-only {
    tr {
      &:first-child {
        display: none;
      }

      th {
        margin-top: 10px;
        text-align: center;
        border: 1px solid $gray-500;
        background: -moz-linear-gradient(top, $white 0%, $gray-200 100%);
        background: -webkit-linear-gradient(top, $white 0%, $gray-200 100%);
        background: -o-linear-gradient(top, $white 0%, $gray-200 100%);
        background: -ms-linear-gradient(top, $white 0%, $gray-200 100%);
        background: linear-gradient(to bottom, $white 0%, $gray-200 100%);
      }
    }

    th, td {
      padding: $table-custom-small-column-padding;
      text-align: left;
    }
  }
}


// Bootstrap
// -------------------------

.table {
  text-align: left;

  tr {
    td:first-child {
      font-weight: 700;
    }
  }

  th tbody, td tbody {
    padding: 8px 18px;
  }

  @include media-breakpoint-up(lg) {
    th, td {
      &:nth-child(n+2) {
        padding-left: 56px;
      }
    }
  }
}


// Table Striped
// -------------------------

.table-striped {
  tbody tr {
    &:nth-of-type(odd) {
      background-color: $table-striped-odd-background;
    }

    &:first-child {
      background-color: transparent;

      th {
        border-top: none;
        border-bottom: 2px solid $gray-500;
      }
    }
  }
}


// Shopping Cart Table
// -------------------------

.table.table-shopping-cart {
  > thead > tr > td{
    padding-top: 20px;
    padding-bottom: 20px;
    border-top-color: $gray-200;
  }

  tr {
    td:first-child {
      font-weight: 700;
    }
  }

  th tbody, td tbody {
    padding: 8px 18px;
  }

  @include media-breakpoint-up(lg) {
    th, td {
      &:nth-child(n+2) {
        padding-left: 0;
      }
    }
  }
}